function strip(html){
    var doc = new DOMParser().parseFromString(html, 'text/html');
    return doc.body.textContent || "";
 }
export default async function changeLanguage(set,theRef){
    let lang = prompt('language');
    let win = (open('https://'.concat(lang.concat('.wikipedia.org')),{mode: 'cors'}));
    let confirmed = await new Promise(c => {    ReactDOM.render(<p><button>Confirm</button></p>);})
if(confirmed)set(lang)
    }